@font-face {
    font-family: "text";
    src: url("../Fonts/LemonMilk.otf");
}
@font-face {
    font-family: "header";
    src: url("../Fonts/soft.otf");
}
@font-face {
    font-family: "title";
    src: url("../Fonts/Ptitle.ttf");
}
html{
    scroll-behavior: smooth;
    font-size: 100%;
}
body{

    color: black;
    font-family: "text";
    margin: 0;
    font-size: 100%;
    padding: 0;
}
nav {
    margin: 0;
    padding: 0;
}
.animation-area{
    position: absolute;
    background: linear-gradient( #525252, #3d72b4);
    width: 100%;
    height: 100%;
    opacity: 1;
    z-index: 0;
}
.animation-area::before{
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(to left, #FFA17F, #00223E);
    z-index: -1;
    transition: opacity 1.5s linear;
    opacity: 0;
}
.animation-area:hover::before{
    opacity: 1;
}
.box-area{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.box-area li {
    position: absolute;
    display: block;
    list-style: none;
    width: 25px;
    height: 25px;
    background: lightgrey;
    animation: animate 20s linear infinite;
    bottom: -150px;
}
.box-area li:nth-child(1){
    left: 86%;
    width: 10%;
    height: 10%;
    animation-delay: 1s;
}
.box-area li:nth-child(2){
    left: 12%;
    top: 5%;
    width: 5%;
    height: 6%;
    animation-delay: 1.5s;
    animation-duration: 10s;
}
.box-area li:nth-child(3){
    left: 70%;
    width: 4%;
    height: 2%;
    animation-delay: 5.5s;
}
.box-area li:nth-child(4){
    left: 70%;
    width: 2%;
    height: 3%;
    animation-delay: 0s;
    animation-duration: 15s;
}
.box-area li:nth-child(5){
    left: 65%;
    width: 1%;
    height: 2%;
    animation-delay: 2s;
}
.box-area li:nth-child(6){
    left: 15%;
    width: 2%;
    height: .5%;
    transform: rotate(90deg);
    animation-delay: 7s;
}
.box-area li:nth-child(7){
    left: 15%;
    top: 15%;
    width: 10%;
    height: 4%;
    transform: rotate(90deg);
    animation-delay: 4s;
}
.box-area li:nth-child(8){
    left: 20%;
    top: 23%;
    width: 4%;
    height: 7%;
    transform: rotate(90deg);
    animation-delay: 0s;
}
@keyframes animate {
    0%{
        transform: translateY(0) translateX(0) rotate(0);
        opacity: 1;
    }
    100%{
        transform: translateY(-800px) translateX(800px) rotate(360deg);
        opactiy: 1;
    }
}
.Page1{
    height: 100vh;
    position: relative;
    margin: 0;
}
.hamburger{
    font-size: 2em;
    z-index: 2;
    position: absolute;
    right: 5%;
    top: 2%;
    display: none;
}
.banner{
    display: flex;
    position: fixed;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
    height: 2%;
    width: 100%;
    text-align: center;
    z-index: 10;
    margin: 0;
    padding: 0;
}
.banner a{
    text-decoration: none;
    color: lavender;
    list-style: none;
    font-size: 2em;
    padding-top: 1%;
    padding-bottom: 0;
    text-align: center;
    transform: translateX(15%);
}
.banner a:hover{
    color: white;
}
.main1{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    height: 93%;
    width: 100%;
}
.hi{
    z-index: 2;
    color: lavender;
    font-family: header;
    font-weight: bold;
    opacity: 0;
    transform: scale(0.5);
    transition: all 1s ease-in-out;
}
.hi.visible
{
    opacity: 1;
    transform: none
}
span{
    text-decoration: underline;
    color: papayawhip;
    font-family: title;
}

