.down{
    text-align: center;
}
ul, li {
    list-style: none;
}
p {
    font-family: header;
}

.about{
    height: 100%;
    width: 100%;
    
    background: linear-gradient( #3d72b4, lightskyblue),
}

.pics{
    border-radius: 50%;
}

.b1{
    display: flex;
    align-items: center;
    height: 20%;
    width: 100%;

}
.b2{
    height: 50%;
    
}
.b2a {
    text-align: center;
    
}

.b2a p ul {
    font-size: .7em;
}
.b2a h3 {
    font-family: header;
}

.pic{
    height: 45%;
    width: 45%;
    box-shadow: 0px 0px 18px -6px black;
}
.pic:hover{
    box-shadow: 0px 0px 40px -6px black;
}
.b1downicon{
    position: absolute;
    bottom: -90%;
    left: 49%;
    opacity: 0.5;

}
.buttons{
    border-radius: 10%;
    padding: 10%;
    background-color: beige;
    font-family: "Dosis";
    border: black solid 1px;
    cursor: pointer;
}
/*  <---- Media Queries ----> */

/*  <---- Mobile Landscape ----> */

@media screen and (min-width: 360px) and (max-width: 666px) {
    .about{
        height: 100%;
        width: 100%;
        
        background: linear-gradient( #3d72b4, lightskyblue),
    }
    .b1{
        display: flex;
        flex-direction: column;
        height: 15%;
        h3 
        {
            color: white;
            margin-top: 10%;
            text-decoration: underline;
        }
    }

    .pics{
        height: 100%;
    }
    .b1element{
        margin-top: 10%;
        text-align: center;
        padding: 0 5% 0 5%;
        color: azure;
    }
    
    .cv
    { 
        height: 100%;
        font-size: 3em;
        margin-top: 10%;
        color: white;
    }
    .b2{
        height: 50%;
        display: grid;
        grid-template-columns: repeat( auto-fit, minmax(1fr, 1fr));
        grid-template-rows: repeat( auto-fit, minmax(1fr, 1fr));
        opacity: 1;
        transition: all 1s ease-in-out;
    }
    .b2a {
        
        height: 100%; 
        width: 100%;
        margin: 10% 0 10% 0;
        font-family: title;
    }
    .b2a h3 
    {
        font-family: header;
        font-size: 2em;
        background-color: azure;
        text-align: center;
        border-radius: 20px;
        border: 2px black solid;
        width: 99%;

    }
    .buttons{
        border-radius: 10%;
        padding: 5%;
        background-color: azure;
        font-family: "Dosis";
        border: black solid 2px;
        cursor: pointer;
        transition: .5s;
        margin-left: 10%;
        margin-top: 5%;
        transform: translateX(-20%);
    }
    .buttons:hover 
    {
        background: black;
        color: white;
        transition: .5s;
    }
    .pic {
        height: auto;
        width: 100%;
        box-shadow: none;
        margin-top: 10%;
        transition: .5s;
    }
    .pic:hover
    {
        background: rgb(53, 53, 53);
        opacity: 0.7;
        transition: .5s;
    }

}

