.contact{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    background-color: lightskyblue;
    width: 100%;
}
.contactmoi{

    font-family: title;
    font-size: 2em;
    background-color: azure;
    text-align: center;
    border-radius: 20px;
    border: 2px black solid;
    width: 99%;

}
.contacticons a{
    color: black;
}
*{
    margin: 0;
    padding: 0;
    font-family: "text";

}

.contact-section h1 {
    text-align: center;
    color: black;
}

.contact-form {

    margin: auto;
    padding: 0 10px;
    overflow: hidden;
}
.contact-form-text{
    display: block;
    width: 100%;
    box-sizing: border-box;
    margin: 16px 0;
    border: 0;
    background: white;
    padding: 20px 40px;
    outline: none;
    color: black;
    transition: 0.5sec;
}
.contact-form-text:focus{
    box-shadow: 0 0 2px 4px #34495e;
}
textarea.contact-form-text{
    resize: none;
    height: 120px;

}
.contact-form-btn{
    float: right;
    border: 0;
    background: #34495e;
    color: #fff;
    padding: 12px 50px;
    border-radius: 20px;
    cursor: pointer;
    transition: 1sec;
}
.contacticons {
    display: flex;
}
.emailed{
    font-size: 2em;
}
.footer{
    width: 100%;
    text-align: center;
    border-top: solid black 2px;
    padding-top: 1%;
    padding-bottom: 1%;
}


/*  <---- Media Queries ----> */

/*  <---- Mobile Landscape ----> */

@media screen and (min-width: 360px) and (max-width: 666px) {
    .contact{
        
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: column;
        height: 150vh;
        background-color: lightskyblue;
        width: 100%;
    }
    .contacticons{
        display: flex;
        justify-content: space-evenly;
        flex-direction: column;
        align-items: center;
    }
    .emailed{
        font-size: 1.2em;
        padding-bottom: 10%;
    }
    .linkedin{
        height: 50%;
    }
    .github{
        height: 50%;
    }
    .contact-section{
        bottom: -161%;
        left: 18%;
    }
}
